import React, { useState } from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import GalleryModal from './GalleryModal';

const GallerySection = styled.section`
  padding: 100px 0;
`;

const SectionTitle = styled.h2`
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 60px;
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 30px;
`;

const ImageWrapper = styled(motion.div)`
  overflow: hidden;
  border-radius: 8px;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
  }
  &:hover img {
    transform: scale(1.1);
  }
`;

const items = [
  {
    src: '/images/image1.jpg',
    title: 'Pure Rotation Scanner',
    description: `    \n\n           .\n\n  :  312Hz\n   : 497.2 rad\n\nAFM( ),      \n\n    ,    .`
  },
  {
    src: process.env.PUBLIC_URL + '/images/image2.jpg',
    title: 'Active Vibration Isolator',
    description: `   \n\n Gimac   \n Matlab    \n Recurdyn   \n Massed    \n\n         .`
  },
  {
    src: process.env.PUBLIC_URL + '/images/image3.jpg',
    title: 'Passive Vibration Isolator',
    description: `   \n\n       ,         .\n\n  (Hydraulic Damper) \n     \n Matlab, Recurdyn         \n\n     .`
  },
  {
    src: process.env.PUBLIC_URL + '/images/image4.jpg',
    title: 'Adjustable Planar Flexure',
    description: `    ( )\n\n           .\n\n m      \n     \n    FEM     3%    \n\n  ,    .`
  },
];

function Gallery() {
  const [selected, setSelected] = useState(null);

  return (
    <GallerySection id="gallery">
      <div className="container">
        <SectionTitle>GALLERY</SectionTitle>
        <Grid>
          {items.map((item, idx) => (
            <ImageWrapper
              key={idx}
              whileHover={{ scale: 1.02 }}
              onClick={() => setSelected(item)}
              style={{ cursor: 'pointer' }}
            >
              <img src={item.src} alt={item.title} />
            </ImageWrapper>
          ))}
        </Grid>
      </div>
      <GalleryModal item={selected} onClose={() => setSelected(null)} />
    </GallerySection>
  );
}

export default Gallery; 